  methods: {
    getDateRange(){
      //console.log(this.minDate)
      let d = new Date(this.dateRange[0]);
      this.lowerDateString = d.toLocaleDateString();
      d = new Date(this.dateRange[1]);
      this.upperDateString = d.toLocaleDateString();
      this.refreshPowerSankey;
    },
    addEnergy(dayType,month){
      let dayCoefficient = 1;
      if(dayType == 'workingDay'){
        dayCoefficient = this.energy.dayTypeCoefficients.workingDays;
      }else if(dayType == 'saturday'){
        dayCoefficient = this.energy.dayTypeCoefficients.saturdays;
      }else if(dayType == 'sunday'){
        dayCoefficient = this.energy.dayTypeCoefficients.sundays;
      }else if(dayType == 'holiday'){
        dayCoefficient = this.energy.dayTypeCoefficients.holidays;
      }else if(dayType == 'breakDay'){
        dayCoefficient = this.energy.dayTypeCoefficients.breakDays;
      }else{
        console.log('Unknown day type')
      };
      return {
        pv: Math.round(dayCoefficient*this.energy.monthCoefficients.pv[month]*this.energy.dailyBase.pv*1000)/1000,
        heating: Math.round(dayCoefficient*this.energy.monthCoefficients.heating[month]*this.energy.dailyBase.heating*1000)/1000,
        waterHeating: Math.round(dayCoefficient*this.energy.monthCoefficients.waterHeating[month]*this.energy.dailyBase.waterHeating*1000)/1000,
        fans: Math.round(dayCoefficient*this.energy.monthCoefficients.fans[month]*this.energy.dailyBase.fans*1000)/1000,
        pumps: Math.round(dayCoefficient*this.energy.monthCoefficients.pumps[month]*this.energy.dailyBase.pumps*1000)/1000,
        battery: Math.round(dayCoefficient*this.energy.monthCoefficients.battery[month]*this.energy.dailyBase.battery*1000)/1000,
        interiorLights: Math.round(dayCoefficient*this.energy.monthCoefficients.interiorLights[month]*this.energy.dailyBase.interiorLights*1000)/1000,
        exteriorLights: Math.round(dayCoefficient*this.energy.monthCoefficients.exteriorLights[month]*this.energy.dailyBase.exteriorLights*1000)/1000,
        elevator: Math.round(dayCoefficient*this.energy.monthCoefficients.elevator[month]*this.energy.dailyBase.elevator*1000)/1000,
        plugOther: Math.round(dayCoefficient*this.energy.monthCoefficients.plugOther[month]*this.energy.dailyBase.plugOther*1000)/1000,
        makerSpace: Math.round(dayCoefficient*this.energy.monthCoefficients.makerSpace[month]*this.energy.dailyBase.makerSpace*1000)/1000,
        computerServices: Math.round(dayCoefficient*this.energy.monthCoefficients.computerServices[month]*this.energy.dailyBase.computerServices*1000)/1000,
        coffeeCart: Math.round(dayCoefficient*this.energy.monthCoefficients.coffeeCart[month]*this.energy.dailyBase.coffeeCart*1000)/1000,
      };
      // this.energy.totals.pv += dayCoefficient*this.energy.monthCoefficients.pv[month]*this.energy.dailyBase.pv;
      // this.energy.totals.heating += dayCoefficient*this.energy.monthCoefficients.heating[month]*this.energy.dailyBase.heating;
      // this.energy.totals.waterHeating += dayCoefficient*this.energy.monthCoefficients.waterHeating[month]*this.energy.dailyBase.waterHeating;
      // this.energy.totals.fans += dayCoefficient*this.energy.monthCoefficients.fans[month]*this.energy.dailyBase.fans;
      // this.energy.totals.pumps += dayCoefficient*this.energy.monthCoefficients.pumps[month]*this.energy.dailyBase.pumps;
      // this.energy.totals.battery += dayCoefficient*this.energy.monthCoefficients.battery[month]*this.energy.dailyBase.battery;
      // this.energy.totals.interiorLights += dayCoefficient*this.energy.monthCoefficients.interiorLights[month]*this.energy.dailyBase.interiorLights;
      // this.energy.totals.exteriorLights += dayCoefficient*this.energy.monthCoefficients.exteriorLights[month]*this.energy.dailyBase.exteriorLights;
      // this.energy.totals.elevator += dayCoefficient*this.energy.monthCoefficients.elevator[month]*this.energy.dailyBase.elevator;
      // this.energy.totals.plugOther += dayCoefficient*this.energy.monthCoefficients.plugOther[month]*this.energy.dailyBase.plugOther;
      // this.energy.totals.makerSpace += dayCoefficient*this.energy.monthCoefficients.makerSpace[month]*this.energy.dailyBase.makerSpace;
      // this.energy.totals.computerServices += dayCoefficient*this.energy.monthCoefficients.computerServices[month]*this.energy.dailyBase.computerServices;
      // this.energy.totals.coffeeCart += dayCoefficient*this.energy.monthCoefficients.coffeeCart[month]*this.energy.dailyBase.coffeeCart;
    },
    getEnergyTotals(energyData){
      this.energy.totals = {
        pv: energyData.reduce((total,arr)=>total+arr.pv,0), //kWh
        heating: energyData.reduce((total,arr)=>total+arr.heating,0), //kwh
        waterHeating: energyData.reduce((total,arr)=>total+arr.waterHeating,0), //kWh
        fans: energyData.reduce((total,arr)=>total+arr.fans,0),
        pumps: energyData.reduce((total,arr)=>total+arr.pumps,0),
        battery: energyData.reduce((total,arr)=>total+arr.battery,0),
        interiorLights: energyData.reduce((total,arr)=>total+arr.interiorLights,0),
        exteriorLights: energyData.reduce((total,arr)=>total+arr.exteriorLights,0),
        elevator: energyData.reduce((total,arr)=>total+arr.elevator,0),
        plugOther: energyData.reduce((total,arr)=>total+arr.plugOther,0),
        makerSpace: energyData.reduce((total,arr)=>total+arr.makerSpace,0),
        computerServices: energyData.reduce((total,arr)=>total+arr.computerServices,0),
        coffeeCart: energyData.reduce((total,arr)=>total+arr.coffeeCart,0),
      };
      // let currentDate = new Date(this.lowerDateString);
      // let endDate = new Date(this.upperDateString);
      // while (currentDate <= endDate){
      //   let monthDayStr = currentDate.toLocaleDateString().substring(0,currentDate.toLocaleDateString().length-5);
      //   if(this.holidays.includes(monthDayStr)){
      //     this.addEnergy('holiday',currentDate.getMonth());
      //   }else if(this.breakDays.includes(monthDayStr)){
      //     this.addEnergy('breakDay',currentDate.getMonth());
      //   }else{
      //     let weekDay = currentDate.getDay();
      //     if(weekDay != 0 && weekDay != 6){
      //       this.addEnergy('workingDay',currentDate.getMonth());
      //     }else if(weekDay == 0){
      //       this.addEnergy('sunday',currentDate.getMonth());
      //     }else if(weekDay == 6){
      //       this.addEnergy('saturday',currentDate.getMonth());
      //     }
      //   }
      //   currentDate.setDate(currentDate.getDate()+1);
      // }
    },
    toggleData(params){
      if(params.data.name=='Lighting'){
        this.lightingBreakdown=!this.lightingBreakdown;
        this.refreshPowerSankey;
      }else if(params.data.name=='Plug'){
        this.plugBreakdown=!this.plugBreakdown;
        this.refreshPowerSankey;
      }else if(params.data.name=='Mechanical & Plumbing'){
        this.MandPBreakdown=!this.MandPBreakdown;
        this.refreshPowerSankey;
      }else if(params.data.name=='Kendeda'){
        this.kendedaBreakdown=!this.kendedaBreakdown;
        this.refreshPowerSankey;
      }
    },
    filterData(params){
      let startIndex = this.dataZoom.getOption().dataZoom[0].startValue;
      let endIndex = this.dataZoom.getOption().dataZoom[0].endValue;
      this.getEnergyTotals(this.createData.slice(startIndex,endIndex));
      this.refreshPowerSankey;
    },
    onResize() {
      if(this.powerSankey) _.debounce(this.powerSankey.resize, 100)()
      if(this.dataZoom)_.debounce(this.dataZoom.resize,100)()
    },

  },
  computed: {
    createData: function(){
      let currentDate = new Date(this.startDate);
      let endDate = new Date(this.endDate);
      endDate.setHours(23);
      let energyData = [];
      let i = 0;
      while (currentDate <= endDate){
        let monthDayStr = currentDate.toLocaleDateString().substring(0,currentDate.toLocaleDateString().length-5);
        energyData.push({date: currentDate.toLocaleString()});
        if(this.holidays.includes(monthDayStr)){
          let fullDayEnergy = this.addEnergy('holiday',currentDate.getMonth());
          let hourlyEnergy = {...fullDayEnergy};
          for(const prop in fullDayEnergy){
            if(currentDate.getHours() < 8 || currentDate.getHours() > 19){
              hourlyEnergy[prop] = Math.round(fullDayEnergy[prop]*.2/12*1000)/1000;
            }else{
              hourlyEnergy[prop] = Math.round(fullDayEnergy[prop]*.8/12*1000)/1000;
            }
          };
          energyData[i] = {...energyData[i],...hourlyEnergy};
        }else if(this.breakDays.includes(monthDayStr)){
          let fullDayEnergy = this.addEnergy('breakDay',currentDate.getMonth());
          let hourlyEnergy = {...fullDayEnergy};
          for(const prop in fullDayEnergy){
            if(currentDate.getHours() < 8 || currentDate.getHours() > 19){
              hourlyEnergy[prop] = Math.round(fullDayEnergy[prop]*.2/12*1000)/1000;
            }else{
              hourlyEnergy[prop] = Math.round(fullDayEnergy[prop]*.8/12*1000)/1000;
            }
          };
          energyData[i] = {...energyData[i],...hourlyEnergy};
        }else{
          let weekDay = currentDate.getDay();
          if(weekDay != 0 && weekDay != 6){
            let fullDayEnergy = this.addEnergy('workingDay',currentDate.getMonth());
            let hourlyEnergy = {...fullDayEnergy};
            for(const prop in fullDayEnergy){
              if(currentDate.getHours() < 8 || currentDate.getHours() > 19){
                hourlyEnergy[prop] = Math.round(fullDayEnergy[prop]*.2/12*1000)/1000;
              }else{
                hourlyEnergy[prop] = Math.round(fullDayEnergy[prop]*.8/12*1000)/1000;
              }
            };
            energyData[i] = {...energyData[i],...hourlyEnergy};
          }else if(weekDay == 0){
            let fullDayEnergy = this.addEnergy('sunday',currentDate.getMonth());
            let hourlyEnergy = {...fullDayEnergy};
            for(const prop in fullDayEnergy){
              if(currentDate.getHours() < 8 || currentDate.getHours() > 19){
                hourlyEnergy[prop] = Math.round(fullDayEnergy[prop]*.2/12*1000)/1000;
              }else{
                hourlyEnergy[prop] = Math.round(fullDayEnergy[prop]*.8/12*1000)/1000;
              }
            };
            energyData[i] = {...energyData[i],...hourlyEnergy};
          }else if(weekDay == 6){
            let fullDayEnergy = this.addEnergy('saturday',currentDate.getMonth());
            let hourlyEnergy = {...fullDayEnergy};
            for(const prop in fullDayEnergy){
              if(currentDate.getHours() < 8 || currentDate.getHours() > 19){
                hourlyEnergy[prop] = Math.round(fullDayEnergy[prop]*.2/12*1000)/1000;
              }else{
                hourlyEnergy[prop] = Math.round(fullDayEnergy[prop]*.8/12*1000)/1000;
              }
            };
            energyData[i] = {...energyData[i],...hourlyEnergy};
          }
        }
        currentDate.setHours(currentDate.getHours()+1);
        i++;
      }
      return energyData;
    },
    refreshDataZoom: function(){
      this.dataZoom.showLoading()
      let length = [];
      length.length = this.createData.length;
      let dataZoomOption = {
        grid: {
          bottom: 20,
        },
        xAxis: {
          type: 'category',
          data: this.createData.map(({date}) => new Date(date).toLocaleDateString()),
          show: true,
        },
        yAxis: {
          show: false,
        },
        dataZoom: [
          {
            name: 'dataZoom',
            type: 'slider',
            show: true,
            start: 0,
            end: 100,
            handleSize: 30,
            filterMode: 'empty',
            textStyle: {
              fontWeight: 'bold',
              fontSize: 16,
            }
          },
        ],
        series: [
          {
            name: 'dataZoom',
            type: 'line',
            data: length.fill(0),
            showSymbol: false,
          }
        ]
      };
      this.dataZoom.setOption(dataZoomOption,true);
      this.dataZoom.hideLoading();
    },
    refreshPowerSankey: function(){
      this.powerSankey.showLoading();
      // console.log(this.createData.map(({pv}) => pv))
      // console.log(this.createData.map(({date}) => new Date(date).toLocaleDateString()))
      let kendedaEnergy = this.energy.totals.heating+this.energy.totals.waterHeating+this.energy.totals.fans+this.energy.totals.pumps+this.energy.totals.plugOther+this.energy.totals.makerSpace+this.energy.totals.coffeeCart+this.energy.totals.computerServices+this.energy.totals.interiorLights+this.energy.totals.exteriorLights+this.energy.totals.elevator+this.energy.totals.battery;
      let powerSankeyOption = {
          tooltip: {
            trigger: 'item',
            triggerOn: 'mousemove'
          },
          series: [
          {
            type: 'sankey',
            focusNodeAdjacency: 'allEdges',
            nodeAlign: 'left',
            lineStyle: {
                normal: {
                    color: 'source',
                    curveness: 0.5
                }
            },
            // data from MV Tables 2-28-19 rev0
            data: this.sankeyData,
            links: this.sankeyLinks,
            animation: true,
          },
        ],
        animation: true,
      };
      if(Math.round(this.energy.totals.pv - kendedaEnergy)>0){
        powerSankeyOption.series[0].links.push({source: 'Solar Panels', target: 'Campus Grid', value: Math.round(this.energy.totals.pv - kendedaEnergy)});
      }else if(Math.round(this.energy.totals.pv - kendedaEnergy)<0){
        powerSankeyOption.series[0].links.push({source: 'Campus Grid', target: 'Kendeda', value: Math.round(kendedaEnergy-this.energy.totals.pv)});
      }else{
        powerSankeyOption.series[0].links.push({source: 'Solar Panels', target: 'Campus Grid', value: 0, linestyle: {opacity: 0}});
      }
      this.powerSankey.setOption(powerSankeyOption,true);
      this.powerSankey.hideLoading();
    },
    sankeyData: function() {
      let kendedaEnergy = this.energy.totals.heating+this.energy.totals.waterHeating+this.energy.totals.fans+this.energy.totals.pumps+this.energy.totals.plugOther+this.energy.totals.makerSpace+this.energy.totals.coffeeCart+this.energy.totals.computerServices+this.energy.totals.interiorLights+this.energy.totals.exteriorLights+this.energy.totals.elevator+this.energy.totals.battery;
      let data = [
        {name: 'Solar Panels', value: Math.round(this.energy.totals.pv)},
        {name: 'Kendeda', value: Math.round(kendedaEnergy)},
        {name: 'Mechanical & Plumbing', value: Math.round(this.energy.totals.heating+this.energy.totals.waterHeating+this.energy.totals.fans+this.energy.totals.pumps)},
        {name: 'Plug', value: Math.round(this.energy.totals.plugOther+this.energy.totals.makerSpace+this.energy.totals.coffeeCart+this.energy.totals.computerServices)},
        {name: 'Lighting', value: Math.round(this.energy.totals.interiorLights+this.energy.totals.exteriorLights)},
        {name: 'Heating', value: Math.round(this.energy.totals.heating)},
        {name: 'Cooling', value: null},
        {name: 'Hot Water', value: Math.round(this.energy.totals.waterHeating)},
        {name: 'Fans', value: Math.round(this.energy.totals.fans)},
        {name: 'Pumps', value: Math.round(this.energy.totals.pumps)},
        {name: 'Water Treatment', value: null},
        {name: 'Maker Space', value: Math.round(this.energy.totals.makerSpace)},
        {name: 'Coffee Cart', value: Math.round(this.energy.totals.coffeeCart)},
        {name: 'Computer Services', value: Math.round(this.energy.totals.computerServices)},
        {name: 'Miscellaneous', value: Math.round(this.energy.totals.plugOther)},
        {name: 'Interior Lighting', value: Math.round(this.energy.totals.interiorLights)},
        {name: 'Exterior Lighting', value: Math.round(this.energy.totals.exteriorLights)},
        {name: 'Elevator', value: Math.round(this.energy.totals.elevator)},
        {name: 'Battery Panel', value: Math.round(this.energy.totals.battery)},
        {name: 'Campus Grid', value: Math.abs(Math.round(this.energy.totals.pv - kendedaEnergy))}
      ];
      if (!this.MandPBreakdown){
        data = data.filter(e => e.name !== 'Heating' && e.name !== 'Hot Water' && e.name !== 'Fans' && e.name !== 'Pumps' && e.name !== 'Cooling' && e.name !== 'Water Treatment');
      };
      if (!this.plugBreakdown){
        data = data.filter(e => e.name !== 'Maker Space' && e.name !== 'Coffee Cart' && e.name !== 'Computer Services' && e.name !== 'Miscellaneous');
      };
      if (!this.lightingBreakdown){
        data = data.filter(e => e.name !== 'Interior Lighting' && e.name !== 'Exterior Lighting');
      };
      if (!this.kendedaBreakdown){
        data = data.filter(e => e.name == 'Solar Panels' || e.name == 'Kendeda' || e.name == 'Campus Grid');
      }
      return data;
    },
    sankeyLinks: function(){
      let kendedaEnergy = this.energy.totals.heating+this.energy.totals.waterHeating+this.energy.totals.fans+this.energy.totals.pumps+this.energy.totals.plugOther+this.energy.totals.makerSpace+this.energy.totals.coffeeCart+this.energy.totals.computerServices+this.energy.totals.interiorLights+this.energy.totals.exteriorLights+this.energy.totals.elevator+this.energy.totals.battery;
      return [
        {source: 'Solar Panels', target: 'Kendeda', value: Math.round(kendedaEnergy)},
        {source: 'Kendeda', target: 'Mechanical & Plumbing', value: Math.round(this.energy.totals.heating+this.energy.totals.waterHeating+this.energy.totals.fans+this.energy.totals.pumps)},
        {source: 'Kendeda', target: 'Plug', value: Math.round(this.energy.totals.plugOther+this.energy.totals.makerSpace+this.energy.totals.coffeeCart+this.energy.totals.computerServices)},
        {source: 'Kendeda', target: 'Lighting', value: Math.round(this.energy.totals.interiorLights+this.energy.totals.exteriorLights)},
        {source: 'Kendeda', target: 'Elevator', value: Math.round(this.energy.totals.elevator)},
        {source: 'Kendeda', target: 'Battery Panel', value: Math.round(this.energy.totals.battery)},
        {source: 'Mechanical & Plumbing', target: 'Heating', value: Math.round(this.energy.totals.heating)},
        {source: 'Mechanical & Plumbing', target: 'Cooling', value: 0},
        {source: 'Mechanical & Plumbing', target: 'Hot Water', value: Math.round(this.energy.totals.waterHeating)},
        {source: 'Mechanical & Plumbing', target: 'Fans', value: Math.round(this.energy.totals.fans)},
        {source: 'Mechanical & Plumbing', target: 'Pumps', value: Math.round(this.energy.totals.pumps)},
        {source: 'Mechanical & Plumbing', target: 'Water Treatment', value: 0},
        {source: 'Plug', target: 'Maker Space', value: Math.round(this.energy.totals.makerSpace)},
        {source: 'Plug', target: 'Coffee Cart', value: Math.round(this.energy.totals.coffeeCart)},
        {source: 'Plug', target: 'Computer Services', value: Math.round(this.energy.totals.computerServices)},
        {source: 'Plug', target: 'Miscellaneous', value: Math.round(this.energy.totals.plugOther)},
        {source: 'Lighting', target: 'Interior Lighting', value: Math.round(this.energy.totals.interiorLights)},
        {source: 'Lighting', target: 'Exterior Lighting', value: Math.round(this.energy.totals.exteriorLights)}
      ];
    },
  },
  mounted: function () {
    echarts.registerTheme('shine', shine)
    this.dataZoom = echarts.init(this.$refs.dataZoom,'shine');
    this.refreshDataZoom;
    this.powerSankey = echarts.init(this.$refs.powerSankey,'shine');
    // this.getEnergyTotals(this.createData);
    this.refreshPowerSankey;
    this.powerSankey.on('click',{seriesIndex: 0},this.toggleData);
    this.dataZoom.on('dataZoom',this.filterData);
    //below didn't work because the first event also triggered the 2nd & 3rd
    // this.powerSankey.on('click',{name: 'Lighting'},this.toggleLighting);
    // this.powerSankey.on('click',{name: 'Mechanical & Plumbing'},this.toggleMandP);
    // this.powerSankey.on('click',{name: 'Plug'},this.togglePlug);
    this.onResize();
  }
}
</script>
